// apiClient.js

import axios from "axios";

const apiClient = axios.create({
    baseURL: 'http://127.0.0.1:2309/', // a ch my ch
    timeout: 10000, // Thi gian ch ti a cho mi yu cu (ms)
    headers: {
        'Content-Type': 'application/json', // Loi d liu c gi i
        'Accept': 'application/json', // Loi d liu c chp nhn t my ch
    },
});

// Export apiClient  c th s dng trc tip trong cc module khc
export default apiClient;
